import { Component, OnInit } from '@angular/core';
import {
  trigger,
  state,
  style,
  animate,
  transition,
  // ...
} from '@angular/animations';

@Component({
  selector: 'app-security',
  templateUrl: './security.component.html',
  styleUrls: ['./security.component.scss'],
  animations: [
    trigger('showHide', [
      state('show', style({
        opacity: 100
      })),
      state('hide', style({
        opacity: 0
      })),
      transition('show <=> hide', [
        animate('0.5s')
      ]),
    ])
  ]
})
export class SecurityComponent implements OnInit {

  show = false;

  constructor() { }

  ngOnInit(): void {
    window.scrollTo(0, 0);
    setTimeout(() => {
      this.show = true;
    }, 700);
  }

  toggle() {
    this.show = !this.show;
  }

}
