import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, ParamMap, Router } from '@angular/router';
import {
  trigger,
  state,
  style,
  animate,
  transition,
  keyframes
} from '@angular/animations';
import { Subscription } from 'rxjs';
import { PersistenceService, SupportedLanguages } from '../services/persistence.service';

import { Questionnaire, Questions } from '../questionnaire/questionnaire';
import { questionnaireDE } from '../questionnaire/questions-german';
import { questionnaireEN } from '../questionnaire/questions-english';
import { questionnaireES } from '../questionnaire/questions-spanish';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  animations: [
    trigger('showHide', [
      state('show', style({
        opacity: 100
      })),
      state('hide', style({
        opacity: 0
      })),
      transition('show <=> hide', [
        animate('0.5s')
      ]),
    ]),
    trigger('startUpFadeIn', [
      state('true', style({
        opacity: 100
      })),
      state('false', style({
        opacity: 0
      })),
      transition('true <=> false', [
        animate('0.5s')
      ]),
    ]),
    trigger(
      'languageChange',
      [
        transition('void => *', []),   // when the item is created
        transition('* => void', []),   // when the item is removed
        transition('* => *', [         // when the item is changed
          animate(2000, keyframes([
            style({ offset: 0.0, opacity: 0 }),
            style({ offset: 1.0, opacity: 100 }),
          ])),
        ]),
      ]),
  ]
})
export class HomeComponent implements OnInit, OnDestroy {

  heading0 = '';
  heading0Shown = '';
  show = true;
  startUpFadeIn = false;

  questionnaire: Questionnaire;
  selectedQuestionSet: Questions;
  selectedIndex: number;
  repeatQuestions: boolean = false;
  usedIndices: Set<number> = new Set();

  selectedLanguage: SupportedLanguages = SupportedLanguages.GERMAN;

  repeatQuestionsSubscription: Subscription;
  activatedRouteSubscription: Subscription;
  languageSwitchSubscription: Subscription;

  questionType: string;

  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private persistenceService: PersistenceService) {
  }

  ngOnInit(): void {
    this.activatedRouteSubscription = this.activatedRoute.paramMap.subscribe((params: ParamMap) => {
      const questionType = params.get('type');
      this.usedIndices.clear();

      if (questionType) {
        this.questionType = questionType.toLowerCase();
        this.nextQuestion();
      } else {
        this.navigateToCheckin();
      }
    });

    this.repeatQuestionsSubscription = this.persistenceService.getRepeatQuestions().subscribe(value => this.repeatQuestions = value);

    this.languageSwitchSubscription = this.persistenceService.getSelectedLanguage().subscribe((language) => {
      this.selectedLanguage = language;

      switch (this.selectedLanguage) {
        case SupportedLanguages.GERMAN:
          this.questionnaire = questionnaireDE;
          break;

        case SupportedLanguages.ENGLISH:
          this.questionnaire = questionnaireEN;
          break;

        case SupportedLanguages.SPANISH:
          this.questionnaire = questionnaireES;
          break;

        default:
          this.questionnaire = questionnaireDE;
          break;
      }

      this.updateSelectedQuestionSetTranslation();
    });
  }

  ngAfterViewInit() {
    window.scrollTo(0, 0);
    setTimeout(() => {
      this.startUpFadeIn = true;
    }, 700);
  }

  updateRepeatQuestionIsAllowed(value: boolean) {
    this.repeatQuestions = value;
    this.persistenceService.setRepeatQuestions(value);
  }

  nextQuestion() {
    this.show = false;

    setTimeout(() => {
      this.updateSelectedQuestionSetTranslation();

      let newIndex = this.getRandomIndexForQuestions(this.selectedQuestionSet);

      // Save indices of questions that have been displayed if repeating questions are disallowed
      // Make all indices available again if all questions have been answered
      if (!this.repeatQuestions) {
        if (this.usedIndices.size === this.selectedQuestionSet.questions.length) {
          this.usedIndices.clear();
        }

        while (this.usedIndices.has(newIndex)) {
          newIndex = this.getRandomIndexForQuestions(this.selectedQuestionSet);
          this.show = false;
        }
      }

      this.usedIndices.add(newIndex);
      this.selectedIndex = newIndex;

      const questionsLeft = this.selectedQuestionSet.questions.length - this.usedIndices.size;
      console.log("Questions left: ", questionsLeft);

      this.heading0Shown = this.heading0;
    }, 1500);

    setTimeout(() => {
      this.show = true;
    }, 1800);
  }

  private updateSelectedQuestionSetTranslation() {
    switch (this.questionType) {
      case 'checkin':
        this.selectedQuestionSet = this.questionnaire.checkin;
        break;

      case 'kennenlernen':
        this.selectedQuestionSet = this.questionnaire.kennenlernen;
        break;

      case 'entweder_oder':
        this.selectedQuestionSet = this.questionnaire.entwederOder;
        break;

      case 'zielsetzung':
        this.selectedQuestionSet = this.questionnaire.zielsetzung;
        break;

      case 'teamarbeit':
        this.selectedQuestionSet = this.questionnaire.teamarbeit;
        break;

      case 'kontroversen':
        this.selectedQuestionSet = this.questionnaire.kontroversen;
        break;

      case 'gruppenreflexion':
        this.selectedQuestionSet = this.questionnaire.gruppenreflexion;
        break;

      case 'checkout':
        this.selectedQuestionSet = this.questionnaire.checkout;
        break;

      default:
        this.selectedQuestionSet = this.questionnaire.checkin;
        break;
    }
  }

  private getRandomIndexForQuestions(questionSet: Questions): number {
    return Math.round(Math.random() * (questionSet.questions.length - 1));
  }

  private navigateToCheckin() {
    this.router.navigate(['checkin']);
  }

  ngOnDestroy() {
    this.repeatQuestionsSubscription.unsubscribe();
    this.activatedRouteSubscription.unsubscribe();
    this.languageSwitchSubscription.unsubscribe();
  }
}
