import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { animate, keyframes, style, transition, trigger } from '@angular/animations';

import { PersistenceService, SupportedLanguages } from '../services/persistence.service';
import { Questionnaire } from '../questionnaire/questionnaire';
import { questionnaireDE } from '../questionnaire/questions-german';
import { questionnaireEN } from '../questionnaire/questions-english';
import { questionnaireES } from '../questionnaire/questions-spanish';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss'],
  animations: [
    trigger(
      'languageChange',
      [
        transition('void => *', []),   // when the item is created
        transition('* => void', []),   // when the item is removed
        transition('* => *', [         // when the item is changed
          animate(2000, keyframes([ 
            style({ offset: 0.0, opacity: 0 }),
            style({ offset: 1.0, opacity: 100 }),
          ])),
        ]),
      ]),
  ]
})
export class NavbarComponent implements OnInit {

  selectedLanguage: SupportedLanguages = SupportedLanguages.GERMAN;

  checkin: string;
  kennenlernen: string;
  entweder_oder: string;
  zielsetzung: string;
  teamarbeit: string;
  kontroversen: string;
  gruppenreflexion: string;
  checkout: string;

  questionnaire: Questionnaire = questionnaireDE;

  private languageSwitchSubscription: Subscription;

  constructor(private router: Router, private persistenceService: PersistenceService) { }

  ngOnInit() {
    this.updateCategoryTranslation();

    this.languageSwitchSubscription = this.persistenceService.getSelectedLanguage().subscribe((language) => {
      this.selectedLanguage = language;
      this.updateCategoryTranslation();
    });
  }

  navigateToRoot() {
    this.router.navigate(['/checkin']);
  }

  notViewingImpressumOrDatenschutz() {
    return (this.router.url !== '/security') && (this.router.url !== '/impressum')
  }

  private updateCategoryTranslation() {
    switch (this.selectedLanguage) {
      case SupportedLanguages.GERMAN:
        this.questionnaire = questionnaireDE;
        break;

      case SupportedLanguages.ENGLISH:
        this.questionnaire = questionnaireEN;
        break;

      case SupportedLanguages.SPANISH:
        this.questionnaire = questionnaireES;
        break;

      default:        
        this.questionnaire = questionnaireDE;
        break;
    }
  }

  ngOnDestroy() {
    this.languageSwitchSubscription.unsubscribe();
  }
}
