import { Component, OnInit } from '@angular/core';
import { PersistenceService, SupportedLanguages } from 'src/app/services/persistence.service';

@Component({
  selector: 'language-switcher',
  templateUrl: './language-switcher.component.html',
  styleUrls: ['./language-switcher.component.scss']
})
export class LanguageSwitcherComponent implements OnInit {

  // Angular doesn't support accessing enums from within templates
  // We use this help attributes to enable strict type checking within selectLanguage,
  // but avoid matching strings with enums
  LANG_GERMAN = SupportedLanguages.GERMAN;
  LANG_ENGLISH = SupportedLanguages.ENGLISH;
  LANG_SPANISH = SupportedLanguages.SPANISH;

  selectedLanguage: SupportedLanguages = SupportedLanguages.GERMAN;

  constructor(private persistenceService: PersistenceService) { }

  ngOnInit(): void { 
    this.persistenceService.getSelectedLanguage().subscribe((language) => this.selectedLanguage = language);
  }

  selectLanguage(language: SupportedLanguages) {
    this.selectedLanguage = language;
    this.persistenceService.setSelectedLanguage(language);
  }
}
