import { Inject, Injectable } from '@angular/core';
import { LOCAL_STORAGE, StorageService } from 'ngx-webstorage-service';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PersistenceService {

  private userEntries: BehaviorSubject<string[]> = new BehaviorSubject([]);
  private repeatQuestions: BehaviorSubject<boolean> = new BehaviorSubject(false);
  private selectedLanguage = new BehaviorSubject<SupportedLanguages>(SupportedLanguages.GERMAN);

  constructor(@Inject(LOCAL_STORAGE) private storage: StorageService) { }

  getUserEntries(): Observable<string[]> {
    const savedValue = this.storage.get(PersistenceCategories.USER_ENTRIES);

    if (savedValue) {
      this.userEntries.next(savedValue);
    }

    return this.userEntries;
  }

  setUserEntries(value: string[]) {
    this.storage.set(PersistenceCategories.USER_ENTRIES, value);
    this.userEntries.next(value);
  }

  getRepeatQuestions(): Observable<boolean> {
    const savedValue = this.storage.get(PersistenceCategories.REPEAT_QUESTIONS);

    if (savedValue) {
      this.repeatQuestions.next(savedValue);
    }

    return this.repeatQuestions;
  }

  setRepeatQuestions(value: boolean) {
    this.storage.set(PersistenceCategories.REPEAT_QUESTIONS, value);
    this.repeatQuestions.next(value);
  }

  getSelectedLanguage(): Observable<SupportedLanguages> {
    const savedValue = this.storage.get(PersistenceCategories.SELECTED_LANGUAGE);

    if (savedValue) {
      this.selectedLanguage.next(savedValue);
    }

    return this.selectedLanguage;
  }

  setSelectedLanguage(value: SupportedLanguages) {
    this.storage.set(PersistenceCategories.SELECTED_LANGUAGE, value);
    this.selectedLanguage.next(value);
  }
}

export enum PersistenceCategories {
  SELECTED_LANGUAGE = "SELECTED_LANGUAGE",
  REPEAT_QUESTIONS = "REPEAT_QUESTIONS",
  USER_ENTRIES = "USER_ENTRIES"
}

export enum SupportedLanguages {
  GERMAN = "GERMAN",
  ENGLISH = "ENGLISH",
  SPANISH = "SPANISH"
}